/* eslint-disable @typescript-eslint/no-unused-vars */
import { CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Card, CardHeader, CardContent, Button } from "@mui/material";
import { Edit2 } from "lucide-react";
import StatusBadge from "../ListeSoutenances/StatusBadge";
import { Badge } from "@/components/ui/badge";

interface Soutenance {
  idSoutenance: number;
  date: string;
  time: string;
  location: string;
  nomGroupe: string;
  juryNames: string[]; // Ensuring it's always an array
  status: string;
}

export default function SoutenancesPage() {
  // Static soutenances data
  const soutenances: Soutenance[] = [
    {
      idSoutenance: 1,
      date: "2025-03-10",
      time: "10:00",
      location: "Salle 1",
      nomGroupe: "Groupe A",
      juryNames: ["Dr. Smith", "Dr. John"],
      status: "Scheduled",
    },

  ];

  // Assume this is the group the student belongs to
  const studentGroup = "Groupe A";

  // Filter soutenances to show only those for the student's group
  const studentSoutenances = soutenances.filter(soutenance => soutenance.nomGroupe === studentGroup);

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl">Soutenances de votre groupe: {studentGroup}</CardTitle>
        </div>
      </CardHeader>
      <CardContent className="p-0">
        <div className="relative w-full overflow-auto">
          <Table>
            <TableHeader className="bg-muted/50">
              <TableRow>
                <TableHead className="w-[250px] font-semibold text-lg">Date</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Heure</TableHead>
                <TableHead className="w-[200px] font-semibold text-lg">Lieu</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Groupe</TableHead>
                <TableHead className="w-[300px] font-semibold text-lg">Jury</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Statut</TableHead>
                <TableHead className="w-[150px] font-semibold text-lg">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {studentSoutenances.map((soutenance, index) => (
                <TableRow key={soutenance.idSoutenance ?? `soutenance-${index}`} className="transition-colors hover:bg-muted/50">
                  <TableCell className="font-medium text-lg">
                    {new Date(soutenance.date).toLocaleDateString("fr-FR", { day: "numeric", month: "long", year: "numeric" })}
                  </TableCell>
                  <TableCell className="text-lg">{soutenance.time}</TableCell>
                  <TableCell className="text-lg">{soutenance.location}</TableCell>
                  <TableCell>
                    <Badge variant="secondary" className="font-medium text-lg">
                      {soutenance.nomGroupe}
                    </Badge>
                  </TableCell>
                  <TableCell>{soutenance.juryNames.join(", ")}</TableCell>
                  <TableCell>
                    <StatusBadge status={soutenance.status} />
                  </TableCell>
                  <TableCell>
                    <Button variant="text">
                      <Edit2 className="h-5 w-5" />
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  );
}
